.d_slideBlock {
  width: 370px;
  box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.1);
  position: relative;
}

.d_slideBlock .slick-dots {
  height: 0px;
  display: flex;
  justify-content: center;
  margin-top: 10px;
  margin-left: 5px;
}

.d_slideBlock .slick-dots li button {
  max-height: 10px;
  max-width: 10px;
  border-radius: 12px;
  border: 2px solid #fb8381;
  margin-right: 8px;
}

.d_slideBlock .slick-active button:before {
  content: "";
  background-color: #fb8381;
  max-height: 10px;
  max-width: 10px;
  border-radius: 12px;
  border: 7px solid #fb8381;
  margin-right: 8px;
  opacity: 1;
}

.d_slideBlock .slick-dots li.slick-active button:before {
  opacity: 1.75;
}

.d_slideBlock .slick-dots li button:before {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 20px;
  height: 20px;
  font-family: slick;
  font-size: 6px;
  line-height: 20px;
  text-align: center;
  color: #000;
  opacity: 0.25;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.d_slide {
  position: relative;
}

.d_slide__a {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-decoration: none;
}

.d_slide__portrait {
  height: 400px;
  background-color: #FB8381;
  position: relative;
  width: 100%;
}

.d_slide__portrait img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.d_slide__portrait_span {
  position: absolute;
  display: block;
  bottom: 0;
  padding: 11px;
  background-color: #134859;
  color: #ffffff;
  font-size: 25px;
  font-weight: bold;
}

.d_slide__text {
  padding-top: 20px;
  padding-left: 12px;
  padding-bottom: 40px;
}

.d_slide__text_h {
  font-size: 30px;
  font-weight: bold;
  color: #134859;
  line-height: 35px;
  margin-bottom: 18px;
}

.d_slide__text_p {
  font-family: "Roboto";
  color: #5f5f5f;
  font-size: 15px;
}

@media screen and (max-width: 1200px) {
  .d_flex {
    justify-content: center;
  }

  .d_divHexagon {
    width: 70%;
    height: 600px;
    left: 0;
    margin-left: auto;
    margin-right: auto;
  }

  .d_slideBlock {
    display: none;
  }
}

@media screen and (max-width: 1024px) {
  .d_divHexagon {
    width: 80%;
    height: 600px;
    left: 0;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (max-width: 780px) {
  .d_wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (max-width: 768px) {
  .d_divHexagon {
    display: none;
  }

  .d_slideBlock {
    display: block;
    width: 90%;
  }
}

@media screen and (max-width: 576px) {
  .d_disFirstScreen {
    padding-top: 0px;
    padding-bottom: 20px;
    padding-left: 0;
    padding-right: 0;
    overflow: hidden;
  }

  .d_slideBlock {
    display: block;
    width: 100%;
    box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0);
  }
}

@media screen and (max-width: 568px) {
  .d_flex {
    display: block;
  }
}

@media screen and (max-width: 520px) {
  .d_slideBlock .slick-dots {
    margin-bottom: 35px;
  }

  .d_slide__portrait {
    height: 370px;
  }
}